import React, { useState, useEffect, Fragment } from 'react';
import { Card } from 'react-bootstrap';
import axios from 'axios';
import Masonry from 'react-masonry-css'
import {Loading} from './Util.js'

function Bless() {

    const url = "https://script.google.com/macros/s/AKfycbyFyQ6OrCWSRDdE28OfXwhHsExDhPNDdx6qHCsD3GlyE8RjedEL/exec"
  
    // const Colors = ["#79e1d1", "#ffc0c0", "#9fd9ff", "#ffc0f6"]
    // const test = [{ "name": "__", "time": "2020-06-08T10:46:57.130Z", "msg": "!!" }, { "name": "_", "time": "2020-06-10T09:39:34.320Z", "msg": "1010" }, { "name": "G_", "time": "2020-06-12T09:51:34.205Z", "msg": "" }, { "name": "__", "time": "2020-06-12T14:58:26.484Z", "msg": "I love you" }, { "name": "__", "time": "", "msg": "" }, { "name": "__", "time": "", "msg": "" }, { "name": "______", "time": "", "msg": "" }]
  

    // const [data, setData] = useState(test);
    const [data, setData] = useState([]);
    const [init, setInit] = useState(true);

    async function initData() {
      // setTimeout(() => {
      //   setData([{name:123,msg:234}]);
      //   setInit(false)
      // }, 5000);
      const result = await axios.get(url);
      setData(result.data);
      setInit(false)
    }
    
    useEffect(() => {
      initData()
    }, []);
  
  
    const breakpointColumnsObj = {
      default: 4,
      1100: 3,
      700: 2,
      500: 1
    };
  
    return (<div style={{position:"relative"}}>
      <div style={{postiion:"relative",width:"100%" ,height:"80pt",display:init?"block":"none"}}>
        <Loading hide={!init}/>
      </div>

      <Masonry
        breakpointCols={breakpointColumnsObj}
        className="my-masonry-grid"
        columnClassName="my-masonry-grid_column"
      >
        {data.map((x, index) => {
          return <Card key={"card"+index} style={{ backgroundColor: "#ffa9a9", borderRadius: "15px", color: "white" }} className="text-center">
            <Card.Body>
              <Card.Title>{x.name}</Card.Title>
              <Card.Text>
                {x.msg}
              </Card.Text>
            </Card.Body>
          </Card>
        })}
      </Masonry>
    </div>
    )
  }


  export default Bless